import React, {useState, useEffect} from 'react'
import { Carousel} from 'antd';
import {getNoticiaDestacadaService} from "../services/destacado"

const NewsDestacada = () => {

    const [dataNews, setDataNews] = useState([])
    const [loading, setLoading] = useState(true)

    useEffect(() => {

        async function getData(){            
            let {data} = await getNoticiaDestacadaService();
            setDataNews(data)
            setLoading(false)
        }
        getData()
        return function cleanup() {
        };
      },[]);

      const contentStyle = {
        height: '160px',
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        backgroundImage: 'url("https://i.pinimg.com/originals/2d/6d/39/2d6d3954cab6327de170f05c266cac36.png")',        
        objectPosition: "center",
        objectFit: "cover"
      };

        if(loading)
        return <>Loading</> 

        if(!loading && !dataNews)
        return <></> 

        if (!loading && dataNews)
        return (
            <Carousel autoplay>
            {dataNews?.map(cNews => (
                <div name="Hola" key={cNews._id}>
                    <h3 style={contentStyle}>{cNews.idNoticia.titulo}</h3>
                </div>
        
            ))}
            </Carousel>

        )

        return (<></>)
}
    


export default NewsDestacada
